import { Action, ActionPanel, Icon, List } from "@raycast/api";
import { sentenceCase } from "change-case";
import { Component } from "../hooks/use-component-data";

type ReactExamplesViewProps = {
  component: NonNullable<Component["implementations"]["react"]>;
};

export function ReactExamplesView({ component }: ReactExamplesViewProps) {
  return (
    <List
      isShowingDetail
      searchBarPlaceholder={`Search ${component.name} examples...`}
    >
      {component.stories.map((story) => {
        const name = getStoryName(story.id);
        return (
          <List.Item
            key={story.id}
            title={name}
            actions={
              <ActionPanel>
                <Action.CopyToClipboard
                  title="Copy Code"
                  content={story.code}
                  icon={Icon.Clipboard}
                />
                <Action.OpenInBrowser
                  title="Open in Storybook"
                  url={`https://primer.style/react/storybook/?path=/story/${story.id}`}
                />
              </ActionPanel>
            }
            detail={
              <List.Item.Detail
                markdown={`# ${name}\n\`\`\`\n${story.code}\n\`\`\``}
              />
            }
          />
        );
      })}
    </List>
  );
}

function getStoryName(id: string) {
  const parts = id.split("--");
  return sentenceCase(parts[parts.length - 1]);
}
