import {
  Action,
  ActionPanel,
  Color,
  Icon,
  List,
  useNavigation,
} from "@raycast/api";
import { pascalCase, sentenceCase } from "change-case";
import React from "react";
import { useComponentData } from "./hooks/use-component-data";
import { useOcticonData } from "./hooks/use-octicon-data";
import { usePackageData } from "./hooks/use-package-data";
import { ReactExamplesView } from "./views/react-examples";
import { ReactPropsView } from "./views/react-props";

const statusColors: Record<string, Color> = {
  alpha: Color.Orange,
  beta: Color.Yellow,
  stable: Color.Green,
  deprecated: Color.Red,
};

const categories = [
  { title: "All", value: "" },
  { title: "Components", value: "components" },
  { title: "Octicons", value: "octicons" },
  { title: "Packages", value: "packages" },
];

export default function Command() {
  const { push } = useNavigation();
  const [filterValue, setFilterValue] = React.useState("");
  const { isLoading: isComponentDataLoading, components } = useComponentData();
  const { isLoading: isOcticonDataLoading, octicons } = useOcticonData();
  const { isLoading: isPackageDataLoading, packages } = usePackageData();
  return (
    <List
      isLoading={isComponentDataLoading || isOcticonDataLoading}
      isShowingDetail
      searchBarAccessory={
        <List.Dropdown
          tooltip="Filter by category"
          value={filterValue}
          onChange={(value) => setFilterValue(value)}
        >
          {categories.map((category) => (
            <List.Dropdown.Item
              key={category.value}
              title={category.title}
              value={category.value}
            />
          ))}
        </List.Dropdown>
      }
    >
      {!filterValue || filterValue === "components" ? (
        <List.Section title="Components">
          {components.map((component) => (
            <List.Item
              key={component.id}
              title={component.name}
              actions={
                <ActionPanel>
                  <Action.OpenInBrowser
                    title="Open Documentation"
                    url={`https://primer.style/design/components/${component.id}`}
                  />
                  <Action.OpenInBrowser
                    icon={Icon.Pencil}
                    title="Edit Documentation"
                    url={`https://github.com/primer/design/blob/main/content/components/${component.id}.mdx`}
                  />
                  {component.implementations.react ? (
                    <ActionPanel.Section title="React">
                      <Action.OpenInBrowser
                        title="Open React Documentation"
                        url={`https://primer.style/design/components/${component.id}/react`}
                      />
                      <Action.CopyToClipboard
                        icon={Icon.Clipboard}
                        title="Copy React Component Name"
                        content={component.implementations.react.name}
                      />
                      <Action.OpenInBrowser
                        icon={Icon.Code}
                        title="Open React Source Code"
                        url={`https://github.com/primer/react/blob/main/src/${component.implementations.react.name}`}
                      />
                      <Action
                        icon={Icon.List}
                        title="View React Props"
                        onAction={() =>
                          push(
                            <ReactPropsView
                              // eslint-disable-next-line @typescript-eslint/no-non-null-assertion -- We know this is defined because of the conditional above
                              component={component.implementations.react!}
                              docsUrl={`https://primer.style/design/components/${component.id}/react#props`}
                            />
                          )
                        }
                      />
                      <Action
                        icon={Icon.CodeBlock}
                        title="View React Examples"
                        onAction={() =>
                          push(
                            <ReactExamplesView
                              // eslint-disable-next-line @typescript-eslint/no-non-null-assertion -- We know this is defined because of the conditional above
                              component={component.implementations.react!}
                            />
                          )
                        }
                      />
                    </ActionPanel.Section>
                  ) : null}
                </ActionPanel>
              }
              detail={
                <List.Item.Detail
                  markdown={`# ${component.name}\n${
                    component.description || ""
                  }`}
                  metadata={
                    <List.Item.Detail.Metadata>
                      {component.implementations.react ? (
                        <>
                          <List.Item.Detail.Metadata.Label
                            title="React Implementation"
                            icon={{
                              source: Icon.Dot,
                              tintColor:
                                statusColors[
                                  component.implementations.react.status
                                ] || Color.SecondaryText,
                            }}
                            text={{
                              value: sentenceCase(
                                component.implementations.react.status
                              ),
                              color:
                                statusColors[
                                  component.implementations.react.status
                                ] || Color.SecondaryText,
                            }}
                          />
                          <List.Item.Detail.Metadata.Label
                            title="React Accessibility"
                            icon={
                              component.implementations.react.a11yReviewed
                                ? {
                                    source: Icon.Check,
                                    tintColor: Color.Purple,
                                  }
                                : undefined
                            }
                            text={{
                              value: component.implementations.react
                                .a11yReviewed
                                ? "Reviewed"
                                : "Not reviewed",
                              color: component.implementations.react
                                .a11yReviewed
                                ? Color.Purple
                                : Color.SecondaryText,
                            }}
                          />
                        </>
                      ) : (
                        <List.Item.Detail.Metadata.Label
                          title="React Implementation"
                          text={{
                            value: "Not available",
                            color: Color.SecondaryText,
                          }}
                        />
                      )}
                    </List.Item.Detail.Metadata>
                  }
                />
              }
            />
          ))}
        </List.Section>
      ) : null}
      {!filterValue || filterValue === "octicons" ? (
        <List.Section title="Octicons">
          {octicons.map((octicon) => {
            const defaultSize =
              "16" in octicon.heights
                ? 16
                : Math.min(...Object.keys(octicon.heights).map(parseInt));
            const imageSource = `https://unpkg.com/@primer/octicons/build/svg/${octicon.name}-${defaultSize}.svg`;
            const reactSnippet = `<${pascalCase(octicon.name)}Icon />`;
            const railsSnippet = `<%= render(Primer::Beta::Octicon.new(:"${octicon.name}")) %>`;

            return (
              <List.Item
                key={octicon.name}
                title={octicon.name}
                keywords={octicon.keywords}
                icon={{
                  source: imageSource,
                  tintColor: Color.PrimaryText,
                }}
                actions={
                  <ActionPanel>
                    <Action.CopyToClipboard
                      title="Copy Icon Name"
                      content={octicon.name}
                    />
                    <Action.CopyToClipboard
                      title="Copy React Snippet"
                      content={reactSnippet}
                    />
                    <Action.CopyToClipboard
                      title="Copy Rails Snippet"
                      content={railsSnippet}
                    />
                    {Object.keys(octicon.heights).map((size) => (
                      <Action.CopyToClipboard
                        key={size}
                        title={`Copy ${size}px SVG`}
                        content={`<svg width="${size}" height="${size}" viewBox="0 0 ${size} ${size}" xmlns="http://www.w3.org/2000/svg">${octicon.heights[size].path}</svg>`}
                      />
                    ))}
                    <Action.OpenInBrowser
                      title="Open Documentation"
                      url={`https://primer.style/design/foundations/icons/${octicon.name}-${defaultSize}`}
                    />
                    <Action.OpenInBrowser
                      title="Open SVG File"
                      url={`https://github.com/primer/octicons/blob/main/icons/${octicon.name}-${defaultSize}.svg`}
                    />
                    <Action.OpenInBrowser
                      icon={Icon.Pencil}
                      title="Edit Keywords"
                      url="https://github.com/primer/octicons/edit/main/keywords.json"
                    />
                  </ActionPanel>
                }
                detail={
                  <List.Item.Detail
                    markdown={`# ${octicon.name}`}
                    metadata={
                      <List.Item.Detail.Metadata>
                        <List.Item.Detail.Metadata.TagList title="Sizes">
                          {Object.keys(octicon.heights).map((size) => (
                            <List.Item.Detail.Metadata.TagList.Item
                              key={size}
                              text={`${size}px`}
                            />
                          ))}
                        </List.Item.Detail.Metadata.TagList>
                        <List.Item.Detail.Metadata.TagList title="Keywords">
                          {octicon.keywords.map((keyword) => (
                            <List.Item.Detail.Metadata.TagList.Item
                              key={keyword}
                              text={keyword}
                            />
                          ))}
                        </List.Item.Detail.Metadata.TagList>
                      </List.Item.Detail.Metadata>
                    }
                  />
                }
              />
            );
          })}
        </List.Section>
      ) : null}
      {!filterValue || filterValue === "packages" ? (
        <List.Section title="Packages">
          {packages.map((pkg) => (
            <List.Item
              key={pkg.name}
              title={pkg.name}
              detail={
                <List.Item.Detail
                  markdown={`# ${pkg.name}`}
                  metadata={
                    <List.Item.Detail.Metadata>
                      {/* TODO */}
                      {/* <List.Item.Detail.Metadata.Label
                        title="Version"
                        text="v0.0.0"
                      />
                      <List.Item.Detail.Metadata.Label
                        title="Last Published"
                        text="2020-01-01"
                      /> */}
                      <List.Item.Detail.Metadata.Label
                        title="Registry"
                        text={pkg.registry}
                      />
                      <List.Item.Detail.Metadata.Label
                        title="Repository"
                        text={pkg.repository}
                      />
                    </List.Item.Detail.Metadata>
                  }
                />
              }
              actions={
                <ActionPanel>
                  <Action.OpenInBrowser
                    icon={Icon.Gift}
                    title="View Release Notes"
                    url={`https://github.com/${pkg.repository}/releases`}
                  />
                  <Action.OpenInBrowser
                    title="Open Repository"
                    url={`https://github.com/${pkg.repository}`}
                  />
                </ActionPanel>
              }
            />
          ))}
        </List.Section>
      ) : null}
    </List>
  );
}
