import { useFetch } from "@raycast/utils";
import { z } from "zod";

const octiconSchema = z.object({
  name: z.string(),
  keywords: z.array(z.string()),
  heights: z.record(
    z.object({
      width: z.number(),
      path: z.string(),
    })
  ),
});

const octiconsSchema = z.record(octiconSchema);

export type Octicon = z.infer<typeof octiconSchema>;

export function useOcticonData() {
  const { isLoading, error, data } = useFetch(
    "https://unpkg.com/@primer/octicons/build/data.json"
  );

  const octicons = data ? octiconsSchema.parse(data) : {};

  return { isLoading, error, octicons: Object.values(octicons) };
}
